import styled from "@emotion/styled";
import { DefaultTableCell, DefaultTableRow } from "app/constants/DefaultTableStyles";
import { Table, TableCell, TableContainer, TableHead, TableRow, Theme, TableBody, tableCellClasses, Button, ButtonOwnProps, TableSortLabel, Tooltip } from "@mui/material";
import { TFunction } from "i18next";
import { CONSTANTS } from "app/constants/TranslationNamespaces";
import { useTranslation } from "react-i18next";
import { BaseSyntheticEvent, useEffect, useRef, useState } from "react";
import moment from "moment";
import { IGenericTable, SortOrder } from "app/constants/DefaultInterfaces";
import { TablePagination } from "@mui/material";
import { makeStyles } from "@mui/styles";
import useWindowDimensions from "app/hooks/useWindowDimensions";
import { motion } from "framer-motion";

const StyledTableCell = styled(TableCell)(({ theme }: { theme?: Theme }) => ({
	...DefaultTableCell(theme, tableCellClasses)
}));

const StyledTableRow = styled(TableRow)(({ theme }: { theme?: Theme }) => ({
	...DefaultTableRow(theme)
}));

export type TableHeading = {
	key: string;
	title: string;
	isDate?: boolean;
	isConstantTranslation?: boolean;
	canBeSorted?: boolean;
	customSortVar?: string;
	tooltipMessage?: string;
	colorCoded?: boolean;
	colorCodeLink?: number | string;
	colors?: Array<string>;
	isHidden?: boolean;
};

export type TableAction = {
	label?: string;
	onClick?: (event: BaseSyntheticEvent) => void;
	icon?: React.ReactNode;
	iconColor?: ButtonOwnProps["color"];
};

type CustomTableProps = {
	sortOrder?: SortOrder;
	sortBy?: string;
	showId: boolean;
	tableLabel: string;
	tableHeadings?: Array<TableHeading>;
	tableRows?: Array<any>;
	t?: TFunction<string, undefined>;
	hasActions?: boolean;
	tableActions?: Array<TableAction>;
	tableInfo?: IGenericTable;
	handlePageChange?: (event: React.MouseEvent<HTMLButtonElement> | null, newPage: number) => void;
	handleSortChange?: (event: React.MouseEvent<unknown>, property: keyof any) => void;
	hasFilterButton?: boolean;
	manyFilters?: boolean;
	hasTabs?: boolean;
	dynamicTableActions?: Array<TableAction>;
	dynamicActionTrigger?: string;
	dynamicActionId?: string;
};

type CustomTableActionButtonsProps = {
	id?: string;
	tableActions?: Array<TableAction>;
};

const CustomTableActionButtons = (props: CustomTableActionButtonsProps) => {
	const { id, tableActions } = props;
	const constantTranslations = useTranslation(CONSTANTS);

	return (
		<div className="flex flex-row gap-8 mr-8">
			{tableActions?.map((action) => (
				<Button
					key={action.label}
					id={id}
					className={`min-w-36`}
					color={action.iconColor}
					onClick={action.onClick}
					aria-label={constantTranslations.t(action.label)}
				>
					{action.icon}
				</Button>
			))}
		</div>
	);
};

const useStyles = makeStyles((theme) => ({
	pagination: {
		"& .MuiToolbar-root": {
			position: "fixed",
			bottom: 0
		},
		"& .MuiTablePagination-spacer": {
			display: "none"
		}
	}
}));

const CustomTable = (props: CustomTableProps) => {
	const ref = useRef<HTMLDivElement>();
	const [tableHeight, setTableHeight] = useState(0);
	const { width, height } = useWindowDimensions();
	const { sortOrder, sortBy, showId, tableLabel, tableHeadings, tableRows, t, hasActions, tableActions, tableInfo, handlePageChange, handleSortChange, hasFilterButton, manyFilters, dynamicActionTrigger, dynamicTableActions, dynamicActionId, hasTabs } =
		props;
	const constantTranslations = useTranslation(CONSTANTS);
	const classes = useStyles();

	const createSortHandler = (property: keyof any) => (event: React.MouseEvent<unknown>) => {
		handleSortChange(event, property);
	};

	useEffect(() => {
		if (width > 900 && width <= 1200) {
			if (hasTabs) {
				setTableHeight(height - 445);
			} else {
				setTableHeight(height - 385);
			}
		} else if (width > 600 && width <= 900) {
			if (hasTabs) {
				setTableHeight(height - 430);
			} else {
				setTableHeight(height - 370);
			}
		} else if (width <= 600) {
			if (hasTabs) {
				setTableHeight(height - 400);
			} else {
				setTableHeight(height - 340);
			}
		} else {
			if (hasFilterButton) {
				setTableHeight(height - 470);
				if (manyFilters) {
					setTableHeight(height - 570);
				}
				if (hasTabs) {
					setTableHeight(height - 480);
				}
			} else {
				setTableHeight(height - 400);
			}
		}
	}, [width, height, hasFilterButton]);

	return (
		<>
			<TableContainer
				ref={ref}
				sx={{ maxHeight: tableHeight }}
			>
				<Table
					aria-label={tableLabel}
					stickyHeader={true}
				>
					<TableHead>
						<TableRow>
							{hasActions && <StyledTableCell key="actions">{constantTranslations.t("ACTIONS")}</StyledTableCell>}
							{tableHeadings?.map((heading) => {
								let title = "";
								if (heading.isConstantTranslation) {
									title = constantTranslations.t(heading.title);
								} else if (t) {
									title = t(heading.title);
								} else {
									title = heading.title;
								}
								if (heading.isHidden) {
									return;
								}
								return (
									<Tooltip
										key={heading.key}
										title={t(heading.tooltipMessage)}
										arrow
									>
										<StyledTableCell>
											{heading.canBeSorted ? (
												<TableSortLabel
													className="whitespace-nowrap"
													active={heading.customSortVar ? sortBy === heading.customSortVar : sortBy === heading.key}
													direction={sortOrder === SortOrder.ASCENDING ? "asc" : "desc"}
													onClick={createSortHandler(heading.customSortVar || heading.key)}
												>
													{title}
												</TableSortLabel>
											) : (
												title
											)}
										</StyledTableCell>
									</Tooltip>
								);
							})}
						</TableRow>
					</TableHead>
					<TableBody>
						{tableRows?.map((row, index) => (
							<StyledTableRow key={row["id"]}>
								{Object.keys(row).map((name) => {
									if (!showId && name.toLowerCase() === "id") {
										return;
									}
									if (name.toLowerCase() === "actions" && row[name] === true) {
										return (
											<StyledTableCell
												padding="checkbox"
												size="small"
												key={`${name}${index}`}
												align="left"
											>
												<CustomTableActionButtons
													id={dynamicActionId && row[dynamicActionId] ? row[dynamicActionId] : row["id"]}
													tableActions={dynamicActionTrigger && row[dynamicActionTrigger] === true ? dynamicTableActions : tableActions}
												/>
											</StyledTableCell>
										);
									}
								})}
								{tableHeadings.map(({ key, isDate, colorCoded, colorCodeLink, colors, isHidden }) => {
									if (isDate) {
										return (
											<StyledTableCell
												padding="none"
												size={typeof row[key] === "number" ? "small" : "medium"}
												key={`${key}${index}`}
												align="left"
											>
												{moment(row[key]).format("Do MMMM YYYY")}
											</StyledTableCell>
										);
									}
									if (typeof row[key] === "boolean") {
										if (row[key]) {
											return (
												<StyledTableCell
													padding="none"
													size={typeof row[key] === "number" ? "small" : "medium"}
													key={`${key}${index}`}
													align="left"
												>
													Yes
												</StyledTableCell>
											);
										} else {
											return (
												<StyledTableCell
													padding="none"
													size={typeof row[key] === "number" ? "small" : "medium"}
													key={`${key}${index}`}
													align="left"
												>
													No
												</StyledTableCell>
											);
										}
									}
									if (colorCoded) {
										return (
											<StyledTableCell
												padding="none"
												size={typeof row[key] === "number" ? "small" : "medium"}
												key={`${key}${index}`}
												align="left"
											>
												<span style={{ color: colors[row[colorCodeLink]] }}>{row[key]}</span>
											</StyledTableCell>
										);
									}
									if (isHidden) {
										return;
									}
									return (
										<StyledTableCell
											padding="none"
											size={typeof row[key] === "number" ? "small" : "medium"}
											key={`${key}${index}`}
											align="left"
										>
											{row[key]}
										</StyledTableCell>
									);
								})}
							</StyledTableRow>
						))}
					</TableBody>
				</Table>
			</TableContainer>
			<motion.div
				variants={{
					visible: { opacity: 1, scale: 1 },
					hidden: { opacity: 0, scale: 0 }
				}}
				whileInView={"visible"}
				initial={"hidden"}
			>
				{tableInfo?.totalCount > 0 && (
					<TablePagination
						rowsPerPageOptions={[tableInfo?.pageSize]}
						count={tableInfo?.totalCount}
						page={tableInfo?.currentPage - 1}
						rowsPerPage={tableInfo?.pageSize}
						onPageChange={(e, newPage) => {
							ref?.current?.scroll({ top: 0, behavior: "smooth" });
							handlePageChange(e, newPage);
						}}
						className={classes.pagination}
					/>
				)}
			</motion.div>
		</>
	);
};

export default CustomTable;
