import CustomDialog, { DialogActionsProp } from "../custom-dialog/CustomDialog";
import { BaseSyntheticEvent, ReactNode, useEffect, useState } from "react";

type CustomFormDialogProps = {
	isGrid?: boolean;
	children?: ReactNode;
	title?: string;
	subtitle?: string;
	handleSubmit?: (e?: BaseSyntheticEvent<object, any, any>) => void | {} | Promise<any>;
	actions?: Array<DialogActionsProp>;
	isValid?: boolean;
	isDirty?: boolean;
};

const CustomFormDialog = (props: CustomFormDialogProps) => {
	const { isGrid, children, title, subtitle, handleSubmit, actions, isDirty, isValid } = props;
	const [customClasses, setCustomClasses] = useState("w-full  items-center ");

	useEffect(() => {
		if (isGrid) {
			setCustomClasses((state) => state + "grid md:grid-cols-4 gap-12");
		} else {
			setCustomClasses((state) => state + "flex flex-col ");
		}

		return () => {
			setCustomClasses("w-full  items-center ");
		};
	}, [isGrid]);

	return (
		<CustomDialog
			isForm={true}
			isDirty={isDirty}
			isValid={isValid}
			title={title}
			subtitle={subtitle}
			actions={actions}
		>
			<form
				className={customClasses}
				onSubmit={handleSubmit}
			>
				{children}
			</form>
		</CustomDialog>
	);
};

export default CustomFormDialog;
