import { TableCellClasses, Theme } from "@mui/material";
import { BG_SLATE_200, BG_SLATE_50 } from "./DefaultColors";

export const DefaultTableCell = (theme: Theme, tableCellClasses: TableCellClasses) => ({
	[`&.${tableCellClasses.head}`]: {
		backgroundColor: BG_SLATE_200,
		color: theme.palette.common.black,
		padding: "0.5rem",
		paddingLeft: 16
	},
	[`&.${tableCellClasses.body}`]: {
		fontSize: 14,
		padding: "0.2rem",
		paddingLeft: 16,
		whiteSpace: "nowrap"
	}
});

export const DefaultTableRow = (theme: Theme) => ({
	"&:nth-of-type(odd)": {
		backgroundColor: BG_SLATE_50
	},
	"&:last-child td, &:last-child th": {
		border: 0
	}
});
