import { TextField } from "@mui/material";
import { makeStyles } from "@mui/styles";
import { useEffect, useState } from "react";
import { Control, Controller, FieldErrors, FieldValues } from "react-hook-form";

type CustomTextFieldProps = {
	control: Control<any, any>;
	name: string;
	label: string;
	errors: FieldErrors<FieldValues>;
	[other: string]: any;
};

const CustomTextField = (props: CustomTextFieldProps) => {
	const { control, name, label, errors, ...other } = props;
	const useStyles = makeStyles({
		customTextField: {
			"& input::placeholder": {
				fontSize: "1rem"
			}
		}
	});
	const [customClass, setCustomClass] = useState("mt-12 shrink-0 ");

	useEffect(() => {
		if (other["dialog"]) {
			setCustomClass((state) => state + "w-full ");
			if (other["calendarSpace"]) {
				setCustomClass((state) => state + "-mt-40 md:mt-12");
			}
			if (other["col1"]) {
				setCustomClass((state) => state + "col-span-2 md:col-span-1 ");
			}
			if (other["col2"]) {
				setCustomClass((state) => state + "col-span-4 md:col-span-2 ");
			}
		} else if (other["double"]) {
			setCustomClass((state) => state + "w-[49%] ");
		} else if (other["filter"]) {
			setCustomClass((state) => state + "w-1/8 ");
		} else {
			setCustomClass((state) => state + "md:mx-6 w-full md:w-[45%] ");
		}
		return () => {
			setCustomClass("mt-12 shrink-0 ");
		};
	}, []);

	return (
		<Controller
			name={name}
			control={control}
			render={({ field }) => (
				<TextField
					{...field}
					aria-label={label}
					classes={{ root: useStyles().customTextField }}
					className={customClass}
					label={label}
					id={name}
					variant="outlined"
					error={!!errors[name]}
					helperText={(errors[name]?.message as string) || " "}
					onChange={(event) => (other["type"] === "number" ? field.onChange(+event.target.value) : field.onChange(event.target.value))}
					aria-required={other["required"] ? "true" : "false"}
					{...other}
				/>
			)}
		/>
	);
};

export default CustomTextField;
