import { apiService as api } from 'app/store/apiService';
import { IBusinessInfo } from './BusinessInfoModel';

export const addTagTypes = ['businessInfo/fetch'] as const;

const BusinessInfoApi = api.enhanceEndpoints({ addTagTypes }).injectEndpoints({
	endpoints: (build) => ({
		getBusinessInfo: build.query<GetBusinessInfoApiResponse, GetBusinessInfoApiArg>({
			query: (businessId) => ({
				url: `/business/${businessId}`
			}),
			providesTags: ['businessInfo/fetch']
		}),
		putBusinessInfo: build.mutation<PutBusinessInfoApiResponse, PutBusinessInfoApiArg>({
			query: (queryArg) => ({
				url: `/business`,
				method: 'PUT',
				data: queryArg
			}),
			invalidatesTags: ['businessInfo/fetch']
		})
	}),
	overrideExisting: false
});

export default BusinessInfoApi;

export type GetBusinessInfoApiResponse = IBusinessInfo;
export type GetBusinessInfoApiArg = string;
export type PutBusinessInfoApiResponse = boolean;
export type PutBusinessInfoApiArg = IBusinessInfo;

export const { useGetBusinessInfoQuery, usePutBusinessInfoMutation } = BusinessInfoApi;

export type BusinessInfoApiType = {
	[BusinessInfoApi.reducerPath]: ReturnType<typeof BusinessInfoApi.reducer>;
};
