export enum SortOrder {
	ASCENDING = "ascending",
	DESCENDING = "descending"
}

export interface IGenericTable {
	totalCount: number;
	totalPages: number;
	currentPage: number;
	pageSize: number;
}

export interface IGenericTableArg {
	page?: number;
	pageSize?: number;
	sortBy?: string;
	sortOrder?: SortOrder;
	filter?: string;
}

export interface IGenericApiResponse {
	message: string;
	status: boolean;
	extraMessage?: any;
}
