import FuseSvgIcon from "@fuse/core/FuseSvgIcon";
import { closeDialog } from "@fuse/core/FuseDialog/fuseDialogSlice";
import { Button, DialogContent, DialogTitle } from "@mui/material";
import { BaseSyntheticEvent, ReactNode } from "react";
import { useDispatch } from "react-redux";
import { SLATE_700 } from "app/constants/DefaultColors";

export type DialogActionsProp = {
	title?: string;
	isSubmit?: boolean;
	onClick?: (e?: BaseSyntheticEvent<object, any, any>) => void | {} | Promise<any>;
	[extras: string]: any;
};

type CustomDialogProps = {
	title?: string;
	children?: ReactNode;
	actions?: Array<DialogActionsProp>;
	isValid?: boolean;
	isDirty?: boolean;
	isForm?: boolean;
	subtitle?: string;
};

const CustomDialog = (prop: CustomDialogProps) => {
	const { title, subtitle, children, actions, isForm, isDirty, isValid } = prop;
	const dispatch = useDispatch();

	return (
		<>
			<DialogTitle>
				<div className="flex flex-col">
					<Button
						className="ml-auto -mr-12"
						onClick={() => dispatch(closeDialog())}
					>
						<FuseSvgIcon
							className="text-48"
							size={24}
							color="inherit"
						>
							material-outline:close
						</FuseSvgIcon>
					</Button>
					<span className="font-bold ml-12 mr-72 md:mr-128 lg:mr-256 whitespace-nowrap">{title}</span>
					<span
						className="ml-12 mr-72 text-12 whitespace-nowrap"
						style={{ color: SLATE_700 }}
					>
						{subtitle}
					</span>
				</div>
			</DialogTitle>
			<DialogContent>
				{children}
				<div className={"flex flex-col md:flex-row justify-end px-12 mb-8"}>
					{actions?.map((action) => {
						let disabled = false;
						if (isForm && !(isValid && isDirty)) {
							disabled = true;
						}
						if (action["forceEnable"]) {
							disabled = false;
						}
						return (
							<Button
								className={`rounded-md ${actions?.length > 1 ? "my-8 md:my-0 md:mx-8 " + actions?.length : "w-full lg:w-1/2"}`}
								key={action.title}
								color="secondary"
								variant={action["variant"] || "contained"}
								onClick={action.onClick}
								disabled={disabled}
							>
								{action.title}
							</Button>
						);
					})}
				</div>
			</DialogContent>
		</>
	);
};

export default CustomDialog;
